<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-button type="primary" @click="resetSearch"></el-button>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-select v-model="filters.major" placeholder="">
						<el-option
							v-for="item in major"
							:key="item.value"
							:label="item.label"
							:value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select v-model="filters.graduationyear" placeholder="">
						<el-option
							v-for="item in graduationyear"
							:key="item.value"
							:label="item.label"
							:value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="excelVisible = true;"></el-button>
				</el-form-item>	
				<el-form-item>
					<el-button type="primary" @click="excelTemplate()"></el-button>
				</el-form-item>	
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;" :border="true">
			<el-table-column type="selection" width="55" align="center">
			</el-table-column>
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
			<el-table-column prop="image" label="" width="180" align="center">
				<template  slot-scope="scope">            
                    <img :src="scope.row.icon"   width="150" height="100" />
                </template>
			</el-table-column>
			<el-table-column prop="name" label="" width="100" align="center">
			</el-table-column>
			<el-table-column prop="sex" label="" width="100" align="center" :formatter="formatSex">
			</el-table-column>
			<el-table-column prop="studentnumber" label="" width="150" align="center">
			</el-table-column>
			<el-table-column prop="major" label="" min-width="180" align="center">
			</el-table-column>
			<el-table-column prop="username" label="" width="180" align="center">
			</el-table-column>
			<el-table-column prop="nickname" label="" min-width="180" align="center">
			</el-table-column>
            <el-table-column prop="graduationyear" label="" min-width="180" align="center">
			</el-table-column>
            <el-table-column prop="correspondenceaddress" label="" min-width="500" align="center">
			</el-table-column>
            <el-table-column prop="phone" label="" min-width="180" align="center">
			</el-table-column>
            <el-table-column prop="wechat" label="" min-width="180" align="center">
			</el-table-column>
            <el-table-column prop="qqnumber" label="QQ" min-width="180" align="center">
			</el-table-column>
            <el-table-column prop="email" label="" min-width="220" align="center">
			</el-table-column>
            <el-table-column prop="graduationplane" label="" min-width="100" align="center">
			</el-table-column>
            <el-table-column prop="usertypenum" label="" min-width="180" align="center">
			</el-table-column>
            <el-table-column prop="canlogin" label="" min-width="180" align="center" :formatter="formatLogin">
			</el-table-column>
            <el-table-column prop="remark" label="" min-width="180" align="center">
			</el-table-column>
			<el-table-column prop="savetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column prop="changetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column label="" width="180" fixed="right" align="center">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handlePasswordVisible(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination  layout="total, sizes, prev, pager, next" 
							@size-change="handleSizeChange"
							@current-change="handleCurrentChange"
							:page-size="2" 
							:page-sizes="[10, 20, 50]" 
							:total="total" 
							style="float:right;">
			</el-pagination>
		</el-col>

		<el-dialog
			title=""
			:close-on-click-modal="false"
			:visible.sync="passwordVisible"
			width="30%">
			<el-input placeholder="" v-model="editForm.password" show-password></el-input>
			<span slot="footer" class="dialog-footer">
				<el-button @click="passwordVisible = false"> </el-button>
				<el-button type="primary" @click="setPassword"> </el-button>
			</span>
		</el-dialog>

		<el-dialog
			title=""
			:close-on-click-modal="false"
			:visible.sync="excelVisible"
			width="21%">
			<el-upload
				ref="upload"
						action="/api/myproject-questionnaire/Upload/excel.action"	
						drag		
						name="excel"
						:on-success="cessUpload"
						multiple>
				<i class="el-icon-upload"></i>
				<div class="el-upload__text"><em></em></div>
				<div class="el-upload__tip" slot="tip">excel500kb</div>
			</el-upload>
		</el-dialog>


		<!---->
		<el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" style=" width: 150px;"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="true"></el-radio>
						<el-radio class="radio" :label="false"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.studentnumber" style=" width: 200px;"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.major" style=" width: 200px;"></el-input>
				</el-form-item>
                <el-form-item label="">
					<el-date-picker
						v-model="editForm.graduationyear"
						type="year"
						placeholder="">
					</el-date-picker>
				</el-form-item>
				 <el-form-item label="">
					<el-input v-model="editForm.username" style=" width: 200px;"></el-input>
				</el-form-item>
                <el-form-item label="">
					<el-input v-model="editForm.nickname" style=" width: 200px;"></el-input>
				</el-form-item>
                <el-form-item label="">

					<el-upload
						ref="upload"
						action="api/myproject-questionnaire/Upload/image.action"			
						name="picture"
						list-type="picture"
						:disabled="hasUpload"
						:limit="1"
						:file-list="myFileList"
						:before-upload="beforeUpload"
						:on-exceed="handleExceed"
						:on-success="successUpload"
						:on-preview="handlePictureCardPreview">
						<el-button slot="trigger" type="primary"></el-button>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
						<img  width="100%" height="100%" :src="dialogImageUrl" alt="">
					</el-dialog>

				</el-form-item>
                <el-form-item label="">
					<el-input v-model="editForm.correspondenceaddress"></el-input>
				</el-form-item>
                <el-form-item label="">
					<el-input v-model="editForm.phone" style=" width: 200px;"></el-input>
				</el-form-item>
                <el-form-item label="">
					<el-input v-model="editForm.wechat" style=" width: 200px;"></el-input>
				</el-form-item>
                <el-form-item label="QQ">
					<el-input v-model="editForm.qqnumber" style=" width: 200px;"></el-input>
				</el-form-item>
                <el-form-item label="">
					<el-input v-model="editForm.email" style=" width: 200px;"></el-input>
				</el-form-item>
                <el-form-item label="">
					<el-col :span="12">
						<el-radio-group v-model="editForm.graduationplane" @change="graduationplaneChange">
							<el-radio class="radio" label=""></el-radio>
							<el-radio class="radio" label=""></el-radio>
							<el-radio class="radio" label=""></el-radio>
							<el-radio class="radio" label=""></el-radio>						
						</el-radio-group>
					</el-col>
					<el-col :span="12">
						<el-input v-model="editForm.graduationplane" placeholder="" v-if="inputShow"></el-input>
					</el-col>		
				</el-form-item>
                <el-form-item>				
						<el-row  v-if="!inputShow">
							<label >{{planeLable}}</label>
						</el-row>
						<el-row  v-if="!inputShow">
							<el-radio-group  v-model="editForm.graduationplanesituation.detail" >
								<el-radio class="radio" label=""></el-radio>
								<el-radio class="radio" label=""></el-radio>
							</el-radio-group>
						</el-row>
						<el-row v-if="editForm.graduationplanesituation.detail == ''  &&  editForm.graduationplane == ''">
							<el-col :span="3"><label></label></el-col>
							<el-col :span="9"><el-input v-model="editForm.graduationplanesituation.detailOne" placeholder=""></el-input></el-col>
							<el-col :span="3" style="text-align:center"><label></label></el-col>
							<el-col :span="9"><el-input v-model="editForm.graduationplanesituation.detailSecond" placeholder=""></el-input></el-col>		
						</el-row>
						<el-row v-else-if="editForm.graduationplanesituation.detail == '' ? editForm.graduationplane == '' | editForm.graduationplane == '' : false ">
							<el-col :span="3"><label></label></el-col>
							<el-col :span="9"><el-input v-model="editForm.graduationplanesituation.detailOne" placeholder=""></el-input></el-col>
							<el-col :span="3" style="text-align:center"><label></label></el-col>
							<el-col :span="9"><el-input v-model="editForm.graduationplanesituation.detailSecond" placeholder=""></el-input></el-col>		
						</el-row>		
				</el-form-item>
				
				<el-form-item label="">
					 <el-select v-model="editForm.usertypenum" placeholder="">
						<el-option
							v-for="item in usertype"
							:key="item.value"
							:label="item.label"
							:value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.canlogin">
						<el-radio class="radio" :label="true"></el-radio>
						<el-radio class="radio" :label="false"></el-radio>
					</el-radio-group>
				</el-form-item>
                 <el-form-item label="">
					<el-input v-model="editForm.remark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser, changePassword } from '../../api/api';

	export default {
		data() {
			return {
				major: [  
					{ value: '',label: '' },
					{ value: '',label: '' },
					{ value: '',label: '' },
					{ value: '',label: '' },
				],
				graduationyear: [  
					{ value: '2016',label: '2016' },
					{ value: '2015',label: '2015' },
					{ value: '2014',label: '2014' },
				],
				usertype:[
					{	value: '',label: ''  }, 
					{	value: '',label: ''  }, 
					{	value: '',label: ''  }, 
				],	
				filters: {
					name: '',
					major: '',
					graduationyear: ''
				},
				myFileList:[],
				hasUpload: false,
				dialogImageUrl: '',
				passwordVisible: false,
				excelVisible: false,
      			dialogVisible: false,
				users: [],
				total: 0,
				rows: 20,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},

				
				inputShow: false,
				planeLable: '',
				//
				editForm: {
					userid: null,
					username: '',
					password: '',
					nickname: '',
					studentnumber: '',
					major: '',
					name: '',
					sex: true,
					graduationyear: '',
					correspondenceaddress: '',
					wechat: '',
					phone: '',
					email: '',
					qqnumber: '',
					graduationplane: "",
					graduationplanesituation: {
						detail: '',
						detailOne: '',
						detailSecond: ''
					},
					canlogin: true,
					usertypenum: "",
					remark: ''
				},

			}
		},
		methods: {
			excelTemplate(){
				var yuming = "http://waixingren.online/resource/wordresource/template.xlsx"
        
				// pdf
				window.open(
					"https://view.officeapps.live.com/op/view.aspx?src=" + yuming,
					"_blank"
				);

			},
			cessUpload(response, file, fileList){
				this.excelVisible = false
				this.getUsers();
			},
			graduationplaneChange(val){
				if(val == ""  ||  val == ""  ||  val == ""){
					this.inputShow = false
					this.planeLable = ""+val+""
				}else{
					this.inputShow = true
					this.editForm.graduationplane = '',
					this.editForm.graduationplanesituation = {
						detail: '',
						detailOne: '',
						detailSecond: ''
					}		
				}
			},
			handleExceed(files, fileList) {
				this.$message.warning("")
			},
			successUpload(response, file, fileList){
				this.editForm.icon = response
			},
			beforeUpload(file){
				const isJPG = (file.type === 'image/jpeg') || (file.type === 'image/png');
      			const isLt5M = file.size / 1024 / 1024 < 5;

				if (!isJPG) {
					this.$message.error(' JPG,PNG !');
				}
				if (!isLt5M) {
					this.$message.error(' 5MB!');
				}
				return isJPG && isLt5M;
			},
			handlePasswordVisible(index, row){
				this.passwordVisible = true;
				this.editForm = Object.assign({}, row);
				this.editForm.password = null
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			formatYear(row, column){
				var date=new Date(row.graduationyear);
 				var year=date.getFullYear(row.graduationyear); 
				return year;
			},
			formatLogin(row, column){
				return row.canlogin == 1 ? '' : row.canlogin == 0 ? '' : '';
			},
			formatSex(row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleSizeChange(val) {
				this.rows = val;
				this.getUsers();
     		},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			resetSearch(){
				this.filters.name = '';
				this.filters.major = '';
				this.filters.graduationyear = '';
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
					filtersName: this.filters.name,
					filtersMajor: this.filters.major,
					filtersGraduationyear: this.filters.graduationyear,
					page: this.page,
					rows: this.rows,	
				};
				this.listLoading = true;
				getUserListPage(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.rows;
					// this.users = JSON.parse(this.users)
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
				console.log(this.editForm)
				this.myFileList = [{
					name: this.editForm.nickname,
					url:  this.editForm.icon
				}];
				if(this.editForm.graduationplane == ""  ||  this.editForm.graduationplane == ""  ||  this.editForm.graduationplane == ""){
					this.inputShow = false
					this.planeLable = ""+this.editForm.graduationplane+""
					this.editForm.graduationplanesituation = JSON.parse(this.editForm.graduationplanesituation)
				}else{
					this.editForm.graduationplanesituation = {
						detail: '',
						detailOne: '',
						detailSecond: ''
					}
					this.inputShow = true	
				}
			},
			//
			handleAdd: function () {
				this.editFormVisible = true;
				this.myFileList = [];
				this.editForm = {
					userid: null,
					username: '',
					password: '',
					nickname: '',
					studentnumber: '',
					major: '',
					name: '',
					sex: true,
					graduationyear: '',
					correspondenceaddress: '',
					wechat: '',
					phone: '',
					email: '',
					qqnumber: '',
					graduationplane: "",
					graduationplanesituation: {
						detail: '',
						detailOne: '',
						detailSecond: ''
					},
					canlogin: true,
					usertypenum: "",
					remark: ''
				};
				this.inputShow = false
				this.planeLable = ''
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.graduationyear = (!para.graduationyear || para.graduationyear == '') ? '' : util.formatDate.format(new Date(para.graduationyear), 'yyyy');
							if(para.userid!=null){//ID
								editUser(para).then((res) => {
								this.editLoading = false;
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
								// serviceObject=usertextService.update( $scope.entity ); //  
							}else{
								addUser(para).then((res) => {
								this.editLoading = false;
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
								
							}	
						});
					}
				});
			},
			setPassword: function() {
				let para = Object.assign({}, this.editForm);
				if(para.password == null){
					this.$message({
							message: '',
							type: 'error'
						});
				}else{
					changePassword(para).then((res) => {
						this.$message({
							message: '',
							type: 'success'
						});
					this.getUsers();
					this.passwordVisible = false
					})
				}				
			},			
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.userid).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		excel() {
			
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>

</style>


