<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.rounterChart" clearable placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getRounterChartList"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="rounterCharts" highlight-current-row v-loading="listLoading" style="width: 100%;" :border="true">
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
			<el-table-column prop="image" label="" width="180" align="center">
				<template  slot-scope="scope">            
                    <img :src="scope.row.image"   width="150" height="100" />
                </template>
			</el-table-column>
			<el-table-column prop="rounterchart" label="" width="120" align="center">
			</el-table-column>
			<el-table-column prop="introduce" label="" width="350" align="center">
			</el-table-column>
			<el-table-column prop="url" label="" width="180" align="center">
			</el-table-column>
			<el-table-column prop="width" label="" width="100" sortable align="center">
			</el-table-column>
			<el-table-column prop="height" label="" width="120" sortable align="center"> 
			</el-table-column>
			<el-table-column prop="isshow" label="" width="100" align="center" :formatter="formatIsshow">
			</el-table-column>
			<el-table-column prop="savetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column prop="changetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column label="" width="150" align="center" fixed="right">
				<template scope="scope" >
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="120px" :rules="editFormRules" ref="editForm">
		
				<el-form-item label="" prop="rounterchart">
					<el-input v-model="editForm.rounterchart"></el-input>
				</el-form-item>

				<el-form-item label="">
					<el-upload
						ref="upload"
						action="api/myproject-questionnaire/Upload/image.action"			
						name="picture"
						list-type="picture"
						:disabled="hasUpload"
						:limit="1"
						:file-list="myFileList"
						:on-change="handleChange"
						:before-upload="beforeUpload"
						:on-exceed="handleExceed"
						:on-success="successUpload"
						:on-preview="handlePictureCardPreview"
						:on-remove="handleRemove">
						<el-button slot="trigger" type="primary"></el-button>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible" append-to-body>
						<img  width="100%" height="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>

				<el-form-item label="">
					<el-input type="textarea"  :rows="8" v-model="editForm.introduce"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.url"></el-input>
				</el-form-item>
				<el-form-item label="" >
					<el-input-number v-model="editForm.width" controls-position="right"  :min="1" :max="100"></el-input-number>
				</el-form-item>
				<el-form-item label="" >
					<el-input-number v-model="editForm.height" controls-position="right"  :min="1" :max="100"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.isshow">
						<el-radio class="radio" :label="true"></el-radio>
						<el-radio class="radio" :label="false"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.remark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getRounterChartAll, UpLoadImage ,addRounterChart, editRounterChart, removeRounterChart} from '../../api/api';

	export default {
		data() {
			return {
				myFileList:[],
				hasUpload: false,
				file: '',
				dialogImageUrl: '',
      			dialogVisible: false,
				filters: {
					rounterChart: '',
				},
				rounterCharts: [],
				listLoading: false,
				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					rounterchart: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					rounterchartid: null,
					rounterchart: '',
					image: '',
					width: 1,
					height: 1,
					introduce: '',
					url: '',
					isshow: true,
					remark: ''
				},

			}
		},
		methods: {
			formatIsshow: function (row, column) {
				return row.isshow == 1 ? '' : row.isshow == 0 ? '' : '';
			},
			handleChange(file, fileList){
			},
			handleExceed(files, fileList) {
				this.$message.warning("")
			},
			successUpload(response, file, fileList){
				this.editForm.image = response
			},
			beforeUpload(file){
				const isJPG = (file.type === 'image/jpeg') || (file.type === 'image/png');
      			const isLt5M = file.size / 1024 / 1024 < 5;

				if (!isJPG) {
					this.$message.error(' JPG,PNG !');
				}
				if (!isLt5M) {
					this.$message.error(' 5MB!');
				}
				return isJPG && isLt5M;
			},
			 handleRemove(file, fileList) {
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleSizeChange(val) {
				this.rows = val;
				this.getRounterChartList();
     		},
			handleCurrentChange(val) {
				this.page = val;
				this.getRounterChartList();
			},
			resetSearch(){
				this.filters.name = '';
				this.filters.addr = '';
				this.getRounterChartList();
			},
			//
			getRounterChartList() {
				let para = {
					rounterChart: this.filters.rounterChart,
				};
				this.listLoading = true;
				getRounterChartAll(para).then((res) => {
					this.rounterCharts = res.data;
					this.listLoading = false;

				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: row.rounterchartid };
					removeRounterChart(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getRounterChartList();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {		
				this.editForm = Object.assign({}, row);
				this.myFileList = [{
					name: this.editForm.rounterchart,
					url:  this.editForm.image
				}];
				this.editFormVisible = true;
			},
			//
			handleAdd: function () {
				this.myFileList = [];
				this.editFormVisible = true;
				this.editForm = {
					rounterchartid: null,
					rounterchart: '',
					image: '',
					width: 1,
					height: 1,
					introduce: '',
					url: '',
					isshow: true,
					remark: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.editForm);
							if(para.rounterchartid!=null){//ID
								editRounterChart(para).then((res) => {
									this.editLoading = false;
									this.$message({
										message: '',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getRounterChartList();
								});
							}else{
								addRounterChart(para).then((res) => {
									this.editLoading = false;
									this.$message({
										message: '',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getRounterChartList();
								});	
							}	
						});
					}
				});
			},
		},
		mounted() {
			this.getRounterChartList();
		}
	}
</script>

<style scoped>
</style>


