<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-button type="primary" @click="resetSearch"></el-button>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.questionnairenumber" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.title" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getNaire"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="newItem()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="naires" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;" :border="true">
			<el-table-column type="selection" width="55" align="center">
			</el-table-column>
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
            <el-table-column prop="questionnairenumber" label="" width="180" align="center">
			</el-table-column>
            <el-table-column prop="title" label="" width="220" align="center">
			</el-table-column>
			<el-table-column prop="image" label="" width="180"  align="center">
				<template  slot-scope="scope">            
					<img :src="scope.row.image"   width="150" height="100" />
				</template>
			</el-table-column>
            <el-table-column prop="author" label="" width="160"  align="center">
			</el-table-column>
            <el-table-column prop="click" label="" width="160"  align="center">
			</el-table-column>
             <el-table-column prop="status" label="" width="160"  align="center">
			</el-table-column>
			<el-table-column prop="savetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column prop="changetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column label="" width="480" fixed="right" align="center">
				<template scope="scope">
					<el-button v-if="scope.row.status == '' || scope.row.status == ''" 
					size="small" @click="dataItem(scope.row.questionnaireid)"></el-button>

					<el-button size="small" @click="previewItem(scope.row.questionnaireid)"></el-button>

					<el-button v-if="scope.row.status == ''" size="small" @click="editItem(scope.row)"></el-button>
					<el-button v-if="scope.row.status == ''" size="small" @click="changestatus(scope.row, '')"></el-button>

					<el-button v-if="scope.row.status == ''" size="small" @click="changestatus(scope.row, '')"></el-button>
					<el-button v-if="scope.row.status == ''" size="small" @click="changestatus(scope.row, '')"></el-button>

					<el-button v-if="scope.row.status == ''" size="small" @click="downLoadWord(scope.row)"></el-button>
					<el-button v-if="scope.row.status == ''" size="small" @click="onlinePrint()"></el-button>
					<el-button v-if="scope.row.status == ''" size="small" @click="changestatus(scope.row, '')"></el-button>

				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination  layout="total, sizes, prev, pager, next" 
							@size-change="handleSizeChange"
							@current-change="handleCurrentChange"
							:page-size="2" 
							:page-sizes="[10, 20, 50]" 
							:total="total" 
							style="float:right;">
			</el-pagination>
		</el-col>


	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { batchRemoveNaire, getNaireListPage, changeStatus, updateNaireStatue, download, getNaireTotalDetail    } from '../../api/api';

	export default {
		data() {
			return {
				sels: [],//

				checkList: [],
				questiontypenumber: [  
					{ value: '',label: '' },
					{ value: '',label: '' },
					{ value: '',label: '' },
					{ value: '',label: '' },
				],
				rate: {
					texts: ['', '', '', '', ''],
				},
				questionGroupList: [],
				filters: {
					title: '',
					questionnairenumber: '',
					status: "",
					questiontypenumber: '',
				},
				myFileList:[],
				hasUpload: false,
				dialogImageUrl: '',
      			dialogVisible: false,
				naires: [],
				total: 0,
				rows: 20,
				page: 1,
				listLoading: false,

				Optionsdelete: [],
				Questiondelete: [],
				QuestionGroupdelete: [],

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					title: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},

				//
				editForm: {
					questionnaireid: null,
					questionnairenumber: '',
					title: '',
					secondtitle: '',
					image: '',
					author: '',
					click: '',
					digest: '',
					status: ""
				},
			}
		},
		methods: {
			onlinePrint() {
			var yuming = "http://waixingren.online/resource/wordresource/template.docx"			
				// pdf
				window.open(
					"https://view.officeapps.live.com/op/view.aspx?src=" + yuming,
					"_blank"
				);
			},
			dataItem(id) {
				this.$router.push({name: 'Data', params: {id: id}});
			},
			previewItem(id) {
				this.$router.push({name: 'Fill', params: {id: id}});
			},
			editItem(item) {
				this.$router.push({name: 'Edit', params: {id: item.questionnaireid}});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.questionnaireid).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					let para = { ids: ids };
					batchRemoveNaire(para).then((res) => {
						this.listLoading = false;
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			newItem() {
				this.$router.push({name: 'Edit', params: {id: 0}})
			},
			getNaire() {
				let para = {
					filtersStatus: this.filters.status,
					filtersQuestionnairenumber: this.filters.questionnairenumber,
					filtersTitle: this.filters.title,
					page: this.page,
					rows: this.rows,	
				};
				this.listLoading = true;
				getNaireListPage(para).then((res) => {
					this.total = res.data.total;
					this.naires = res.data.rows;
					console.log(res.data.rows)
					this.listLoading = false;
				});
			},
			changestatus(item, status) {	
				let para = {
					id: item.questionnaireid,
					status: status,
				};	
				updateNaireStatue(para).then((res) => {
					this.getNaire();
				})	
			},
			handleExceed(files, fileList) {
				this.$message.warning("")
			},
			successUpload(response, file, fileList){
				this.editForm.image = response
			},
			beforeUpload(file){
				const isJPG = (file.type === 'image/jpeg') || (file.type === 'image/png');
      			const isLt5M = file.size / 1024 / 1024 < 5;

				if (!isJPG) {
					this.$message.error(' JPG,PNG !');
				}
				if (!isLt5M) {
					this.$message.error(' 5MB!');
				}
				return isJPG && isLt5M;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			handleSizeChange(val) {
				this.rows = val;
				this.getNaire();
     		},
			handleCurrentChange(val) {
				this.page = val;
				this.getNaire();
			},
			resetSearch(){
				this.filters.title = ''
				this.filters.questionnairenumber = ''
				this.getNaire()
			},
			downLoadWord(item) {
				let paraID = {
						id: item.questionnaireid,
				};
				getNaireTotalDetail(paraID).then((res) => {
					var post ={}
					post.title = res.data.title
					var Postquestions = []		
					var questions = res.data.questions
					questions.forEach( (itemQT, index) => {
						var Pquestions = {}
						var Postrowlist = []
						var quess = itemQT.quess
						quess.forEach( (itemQS, index) => {
							var Prowlist = {}
							var Postproportions = []
							var Postavg
							var options = itemQS.options
							var ASsum = 0            //
							var PRsum = 0			 //
							options.forEach( (itemOT, index) => {			
								var answers= itemOT.answers	
								var answersLE = answers.length
								var answersPR = answersLE * (index + 1)
								PRsum = PRsum + answersPR
								ASsum = ASsum + answersLE
							});	
							Postavg	= PRsum/ASsum
							options.forEach( (itemOT, index) => {
								var Pproportions = {}
								var answers= itemOT.answers	
								var answersLE = answers.length
								Pproportions = this.toPercent(answersLE, ASsum)		
								Postproportions.unshift(Pproportions)		
							});	
							Prowlist.content = itemQS.content
							Prowlist.avg = Postavg		
							Prowlist.proportions = Postproportions	
							Postrowlist.push(Prowlist)	
						});	
						Pquestions.degest = itemQT.digest
						Pquestions.end = "this end"
						Pquestions.rowlist = Postrowlist
						Postquestions.push(Pquestions)
					});	
					post.questions = Postquestions
					console.log(post)
					download(post).then(res => {
						var blob = new Blob([res.data], { type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document;charset=utf-8'}); //application/vnd.openxmlformats-officedocument.wordprocessingml.documentdoc
						var downloadElement = document.createElement('a');
						var href = window.URL.createObjectURL(blob); //
						downloadElement.href = href;
						downloadElement.download ='.doc'; //
						// document.body.appendChild(downloadElement);
						downloadElement.click(); //
						window.URL.revokeObjectURL(href); //blob
			    })
				});
			},
			toPercent(num, total) { 
				return (Math.round(num / total * 10000) / 100.00 + "%");// 
			},
		},
		mounted() {
			let curTime = Date.now();
			this.getNaire();
		},
	}

</script>

<style scoped>

</style>


